<template>
    <div>
        <el-form ref="form" :model="form" label-width="140px" :rules="rules">
    
            <el-collapse v-model="collapseActive" @change="">
                <el-collapse-item title="　基本信息" name="1">

                    <el-form-item label="姓名" prop="name">
                        <el-input v-model="form.name" :disabled="isDisabled()"></el-input>
                    </el-form-item>
                    <el-form-item label="性别" prop="gender">
                        <el-radio-group v-model="form.gender" :disabled="isDisabled()">
                            <el-radio label="1">男</el-radio>
                            <el-radio label="2">女</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item label="移动电话" prop="mobile">
                        <el-input v-model="form.mobile" :disabled="isDisabled()"></el-input>
                    </el-form-item>
                    <el-form-item label="固定电话">
                        <el-input v-model="form.fixed_telephone" :disabled="isDisabled()"></el-input>
                    </el-form-item>
                    <el-form-item label="家庭地址">
                        <el-input v-model="form.home_address" :disabled="isDisabled()"></el-input>
                    </el-form-item>
                    <el-form-item label="籍贯">
                        <el-input v-model="form.birth_place" :disabled="isDisabled()"></el-input>
                    </el-form-item>
                    <el-form-item label="紧急联系人">
                        <el-input v-model="form.emer_contact" :disabled="isDisabled()"></el-input>
                    </el-form-item>
                    <el-form-item label="紧急移动电话">
                        <el-input v-model="form.emer_mobile" :disabled="isDisabled()"></el-input>
                    </el-form-item>
                    <el-form-item label="紧急联系人地址">
                        <el-input v-model="form.emer_address" :disabled="isDisabled()"></el-input>
                    </el-form-item>
                    <el-form-item label="担保公司">
                        <el-select v-model="form.part_file_id" placeholder="请选择" class="mr10" :disabled="isDisabled()">
                            <el-option
                                v-for="item in optionsGuaranteeCompany"
                                :key="item.id"
                                :label="item.company_name"
                                :value="item.id">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="担保自然人">
                        <el-select v-model="form.per_owner_id" placeholder="请选择" class="mr10" :disabled="isDisabled()">
                            <el-option
                                v-for="item in optionsGuaranteePeople"
                                :key="item.id"
                                :label="item.name"
                                :value="item.id">
                            </el-option>
                        </el-select>
                    </el-form-item>

                </el-collapse-item>

                <el-collapse-item title="　认证信息" name="2">
                    
                    <el-form-item label="身份证照片" prop="id_card_front">
                        <el-row :gutter="20">
                            <el-col :span="10">
                                <el-upload
                                    :disabled="isDisabled()"
                                    class="avatar-uploader"
                                    :action="IMG_UPLOAD_URL"
                                    :show-file-list="false"
                                    :data="{type:1}"
                                    :on-success="handleUploadSuccess_id_card_front"
                                    :before-upload="handleBeforeUpload">
                                    <img v-if="form.id_card_front" :src="form.id_card_front" class="avatar">
                                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                                </el-upload>
                                <p>身份证正面</p>
                            </el-col>
                            <el-col :span="10">
                                <el-upload
                                    :disabled="isDisabled()"
                                    class="avatar-uploader"
                                    :action="IMG_UPLOAD_URL"
                                    :show-file-list="false"
                                    :data="{type:2}"
                                    :on-success="handleUploadSuccess_id_card_end"
                                    :before-upload="handleBeforeUpload">
                                    <img v-if="form.id_card_end" :src="form.id_card_end" class="avatar">
                                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                                </el-upload>
                                <p>身份证反面</p>
                            </el-col>
                        </el-row>
                    </el-form-item>

                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="身份证号" prop="id_card">
                                <el-input v-model="form.id_card" :disabled="isDisabled()"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="出生日期" prop="birthday">
                                <el-date-picker
                                    :disabled="isDisabled()"
                                    v-model="form.birthday"
                                    type="date"
                                    placeholder="选择日期">
                                </el-date-picker>
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-form-item label="户口本照片">
                        <el-row :gutter="20">
                            <el-col :span="10">
                                <el-upload
                                    :disabled="isDisabled()"
                                    class="avatar-uploader"
                                    :action="IMG_UPLOAD_URL"
                                    :show-file-list="false"
                                    :on-success="handleUploadSuccess_acount_img"
                                    :before-upload="handleBeforeUpload">
                                    <img v-if="form.acount_img" :src="form.acount_img" class="avatar">
                                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                                </el-upload>
                                <p>户主页</p>
                            </el-col>
                            <el-col :span="10">
                                <el-upload
                                    :disabled="isDisabled()"
                                    class="avatar-uploader"
                                    :action="IMG_UPLOAD_URL"
                                    :show-file-list="false"
                                    :on-success="handleUploadSuccess_person_img"
                                    :before-upload="handleBeforeUpload">
                                    <img v-if="form.person_img" :src="form.person_img" class="avatar">
                                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                                </el-upload>
                                <p>个人页</p>
                            </el-col>
                        </el-row>
                    </el-form-item>

                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="户号">
                                <el-input v-model="form.acount_numbers" :disabled="isDisabled()"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-form-item label="结婚证照片">
                        <el-row :gutter="20">
                            <el-col :span="10">
                                <el-upload
                                    :disabled="isDisabled()"
                                    class="avatar-uploader"
                                    :action="IMG_UPLOAD_URL"
                                    :show-file-list="false"
                                    :on-success="handleUploadSuccess_marry_img"
                                    :before-upload="handleBeforeUpload">
                                    <img v-if="form.marry_img" :src="form.marry_img" class="avatar">
                                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                                </el-upload>
                            </el-col>
                        </el-row>
                    </el-form-item>

                    <el-form-item label="房产证照片">
                        <el-row :gutter="20">
                            <el-col :span="10">
                                <el-upload
                                    :disabled="isDisabled()"
                                    class="avatar-uploader"
                                    :action="IMG_UPLOAD_URL"
                                    :show-file-list="false"
                                    :on-success="handleUploadSuccess_house_img"
                                    :before-upload="handleBeforeUpload">
                                    <img v-if="form.house_img" :src="form.house_img" class="avatar">
                                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                                </el-upload>
                            </el-col>
                        </el-row>
                    </el-form-item>

                </el-collapse-item>

                <el-collapse-item title="　从业资格信息" name="6">
                    
                    <el-form-item label="驾驶证照片">
                        <el-row :gutter="20">
                            <el-col :span="10">
                                <el-upload
                                    :disabled="isDisabled()"
                                    class="avatar-uploader"
                                    :action="IMG_UPLOAD_URL"
                                    :show-file-list="false"
                                    :data="{type:3}"
                                    :on-success="handleUploadSuccess_drive_img_front"
                                    :before-upload="handleBeforeUpload">
                                    <img v-if="form.drive_img_front" :src="form.drive_img_front" class="avatar">
                                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                                </el-upload>
                                <p>驾驶证正面</p>
                            </el-col>
                            <el-col :span="10">
                                <el-upload
                                    :disabled="isDisabled()"
                                    class="avatar-uploader"
                                    :action="IMG_UPLOAD_URL"
                                    :show-file-list="false"
                                    :on-success="handleUploadSuccess_drive_img_end"
                                    :before-upload="handleBeforeUpload">
                                    <img v-if="form.drive_img_end" :src="form.drive_img_end" class="avatar">
                                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                                </el-upload>
                                <p>驾驶证反面</p>
                            </el-col>
                        </el-row>
                    </el-form-item>

                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="驾驶证号">
                                <el-input v-model="form.drive_license_number" :disabled="isDisabled()"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="驾驶证到期日期">
                                <el-date-picker
                                    :disabled="isDisabled()"
                                    v-model="form.drive_license_expire"
                                    type="date"
                                    placeholder="选择日期">
                                </el-date-picker>
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="准驾车型">
                                <el-select v-model="form.dict_id" placeholder="请选择" class="mr10" :disabled="isDisabled()">
                                    <el-option
                                        v-for="item in optionsDriveType"
                                        :key="item.id"
                                        :label="item.type_item_name"
                                        :value="item.id">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-form-item label="从业资格照片">
                        <el-row :gutter="20">
                            <el-col :span="10">
                                <el-upload
                                    :disabled="isDisabled()"
                                    class="avatar-uploader"
                                    :action="IMG_UPLOAD_URL"
                                    :show-file-list="false"
                                    :on-success="handleUploadSuccess_qua_img_front"
                                    :before-upload="handleBeforeUpload">
                                    <img v-if="form.qua_img_front" :src="form.qua_img_front" class="avatar">
                                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                                </el-upload>
                                <p>从业资格证正面</p>
                            </el-col>
                            <el-col :span="10">
                                <el-upload
                                    :disabled="isDisabled()"
                                    class="avatar-uploader"
                                    :action="IMG_UPLOAD_URL"
                                    :show-file-list="false"
                                    :on-success="handleUploadSuccess_qua_img_end"
                                    :before-upload="handleBeforeUpload">
                                    <img v-if="form.qua_img_end" :src="form.qua_img_end" class="avatar">
                                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                                </el-upload>
                                <p>从业资格证反面</p>
                            </el-col>
                        </el-row>
                    </el-form-item>

                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="从业资格证号">
                                <el-input v-model="form.qua_cert_number" :disabled="isDisabled()"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="从业资格证到期日期">
                                <el-date-picker
                                    :disabled="isDisabled()"
                                    v-model="form.qua_cert_expire"
                                    type="date"
                                    placeholder="选择日期">
                                </el-date-picker>
                            </el-form-item>
                        </el-col>
                    </el-row>

                </el-collapse-item>

                <el-collapse-item title="　名下车辆列表（公司体系下所有车辆）" name="3" v-if="isShowBlock()">
                    
                    <el-table :data="form.car_info" border class="table" ref="multipleTable">
                        <el-table-column prop="plate_number" label="车牌号" width="150">
                        </el-table-column>
                        <el-table-column prop="vin_number" label="vin" width="120">
                        </el-table-column>
                        <el-table-column prop="car_info" label="车辆信息">
                        </el-table-column>
                        <el-table-column prop="band_name" label="购车公司">
                        </el-table-column>
                        <el-table-column prop="bus_mech_name" label="挂靠公司">
                        </el-table-column>
                        <el-table-column prop="create_time" label="加入时间">
                        </el-table-column>
                    </el-table>

                </el-collapse-item>

                <el-collapse-item title="　征信信息" name="4" v-if="isShowBlock()">
                    
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="历史贷款">
                                {{form.credit_info.history_num}}
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="贷款总额">
                                {{form.credit_info.total_loan_amount}}
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="贷款人数">
                                {{form.credit_info.overdue_number}}
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="是否黑名单">
                                {{form.credit_info.status == 3 ? '是' : '否'}}
                            </el-form-item>
                        </el-col>
                    </el-row>

                </el-collapse-item>

                <el-collapse-item title="　信贷信息" name="5" v-if="isShowBlock()">
                    
                    <el-table :data="form.loan_info" border class="table" ref="multipleTable">
                        <el-table-column prop="contract_num" label="合同号" width="150">
                        </el-table-column>
                        <el-table-column prop="total_loan_amount" label="贷款金额" width="120">
                        </el-table-column>
                        <el-table-column label="贷款性质">
                            <template slot-scope="{row}">
                                {{row.credit_type == 1 ? '助贷' : '自贷'}}
                            </template>
                        </el-table-column>
                        <el-table-column prop="already_pay_count" label="账期信息">
                        </el-table-column>
                        <el-table-column label="是否结清">
                            <template slot-scope="{row}">
                                {{row.is_give == 2 ? '是' : '否'}}
                            </template>
                        </el-table-column>
                        <el-table-column prop="plate_number" label="车牌号">
                        </el-table-column>
                        <el-table-column label="是否挂靠">
                            <template slot-scope="{row}">
                                {{row.cp_affiliat_name == 1 ? '是' : '否'}}
                            </template>
                        </el-table-column>
                        <el-table-column prop="user_id" label="创建人员">
                        </el-table-column>
                        <el-table-column prop="create_time" label="创建时间">
                        </el-table-column>
                    </el-table>

                </el-collapse-item>


            </el-collapse>
            <el-form-item class="form-submit">
                <el-button type="primary" @click="handleSubmit" v-if="pageType != 'view'">保存</el-button>
                <el-button @click="$router.back()">返回</el-button>
            </el-form-item>
        </el-form>
        
    </div>
</template>

<script>
    import * as API from '@/api/assetsFile'
    import { requestUrl } from '@/api/config'
    export default {
        data: function(){
            let validateIdCard = (rule, value, callback)=>{
                if (!value) {
                    return callback(new Error('请输入身份证号'));
                }else{
                    this.loadingIdCrad = true
                    API.apiIndividualFileIdCard({id_card: this.form.id_card}).then(res => {
                        if(res.data){
                            callback();
                        }else{
                            callback(new Error('已经有了'));
                        }
                        this.loadingIdCrad = false
                    })
                }
            }
            return {
                IMG_UPLOAD_URL: `${requestUrl}/home/UpLoadFile/uploadImg`,
                collapseActive: ['1'],
                loadingIdCrad: false,
                pageType: 'add',
                optionsGuaranteePeople: [],
                optionsGuaranteeCompany: [],
                optionsDriveType: [],
                form: {
                    id_card_front: '',
                    id_card_end: '',
                    acount_img: '',
                    person_img: '',
                    marry_img: '',
                    house_img: '',
                    drive_img_front: '',
                    drive_img_end: '',
                    qua_img_front: '',
                    qua_img_end: '',
                    emer_address: '',
                    emer_contact: '',
                    emer_mobile: '',
                    fixed_telephone: '',
                    mobile: '',
                    id_card: '',
                    birthday: '1970-1-1',
                    name: '',
                    gender: '',
                    qua_cert_expire: '',
                    home_address: '',
                    birth_place: '',
                    drive_license_expire: '',
                    part_file_id: '',
                    per_owner_id: '',
                    acount_numbers: '',
                    drive_license_number: '',
                    dict_id: '',
                    qua_cert_number: '',
                    car_info: [],
                    credit_info: {
                        total_loan_amount: "",
                        status: "",
                        history_num: " ",
                        overdue_number: "",
                    },
                    loan_info: []

                },
                rules: {
                    name: [
                        { required: true, message: '请输入姓名', trigger: 'blur' }
                    ],
                    gender: [
                        { required: true, message: '请选择性别', trigger: 'change' }
                    ],
                    mobile: [
                        { required: true, message: '请输入移动电话', trigger: 'blur' }
                    ],
                    id_card_front: [
                        { required: true, message: '请上传身份证正面', trigger: 'blur' }
                    ],
                    id_card: [
                        { required: true, validator: validateIdCard, trigger: 'blur' }
                    ],
                    birthday: [
                        { required: true, message: '请输入出生日期', trigger: 'blur' }
                    ],
                }
            }
        },
        mounted(){
            this.checkPath()
            this.getGuarantee()
            this.getGuaranteeDriveType()
            this.getGuaranteePeople()
        },
        methods: {
            // 是否禁止输入
            isDisabled () {
                let d = this.pageType == 'view' ? true : false;
                return d;
            },
            isShowBlock(){
                return this.pageType == 'view'
            },
            checkPath() {
                let _path = this.$route.path,
                    _query = this.$route.query;
                if(_path.indexOf('add') > -1) {
                    this.pageType = 'add';
                }else if(_path.indexOf('edit') > -1) {
                    this.getFormData();
                    this.pageType = 'edit';
                }else if(_path.indexOf('view') > -1) {
                    this.getFormData();
                    this.pageType = 'view';
                }
            },
            getFormData(){
                API.apiIndividualFileView({id: this.$route.query.id, operation_uniqid: this.$route.query.operation_uniqid}).then(res => {
                    Object.assign(this.form,res.data)
                })
            },
            getGuarantee(){
                API.apiIndividualFileGuarantee({}).then(res => {
                    this.optionsGuaranteeCompany=res.data
                })
            },
             getGuaranteePeople(){
                API.apiIndividualFilePeople({}).then(res => {
                    this.optionsGuaranteePeople=res.data
                })
            },
            getGuaranteeDriveType(){
                API.apiIndividualFileDriveType({}).then(res => {
                    this.optionsDriveType=res.data
                })
            },
            handleUploadSuccess_id_card_front(res, file, fileList){
                this.form.id_card_front = res.data.url
                this.form.id_card = res.data.idCard
                this.form.birthday = res.data.birth
            },
            handleUploadSuccess_id_card_end(res, file, fileList){
                this.form.id_card_end = res.data.url
            },
            handleUploadSuccess_acount_img(res, file, fileList){
                this.form.acount_img = res.data.url
            },
            handleUploadSuccess_person_img(res, file, fileList){
                this.form.person_img = res.data.url
            },
            handleUploadSuccess_marry_img(res, file, fileList){
                this.form.marry_img = res.data.url
            },
            handleUploadSuccess_house_img(res, file, fileList){
                this.form.house_img = res.data.url
            },
            handleUploadSuccess_drive_img_front(res, file, fileList){
                this.form.drive_img_front = res.data.url
                this.form.drive_license_number = res.data.drive_number
                this.form.drive_license_expire = res.data.expire_date
            },
            handleUploadSuccess_drive_img_end(res, file, fileList){
                this.form.drive_img_end = res.data.url
            },
            handleUploadSuccess_qua_img_front(res, file, fileList){
                this.form.qua_img_front = res.data.url
            },
            handleUploadSuccess_qua_img_end(res, file, fileList){
                this.form.qua_img_end = res.data.url
            },
            handleBeforeUpload(){

            },
            handleSubmit() {
                this.$refs.form.validate((valid)=>{
                    if(valid){
                        if(this.pageType == 'add'){
                            API.apiIndividualFileAdd(this.form).then(res => {
                                this.$message.success('提交成功！');
                                this.$router.back()
                            })
                        }else{
                            API.apiIndividualFileEdit(this.form).then(res => {
                                this.$message.success('提交成功！');
                                this.$router.back()
                            })
                        }
                    }else{
                        return false
                    }
                })
            }
        }
    }
</script>

<style lang="scss" scoped>
    
</style>